@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeOut {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

@keyframes up {
    0% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(-20px);
    }
}

@keyframes down {
    0% {
        transform: translateY(-20px);
    }
    100% {
        transform: translateY(0px);
    }
}

@keyframes left {
    0% {
        transform: translateX(80px);
    }
    100% {
        transform: translateX(0);
    }
}

@keyframes right {
    0% {
        transform: translateX(0px);
    }
    100% {
        transform: translateX(80px);
    }
}

.loglines-modal {
    /* Initial state */
    opacity: 0;
    animation-fill-mode: forwards;
    /* Keep the animation state after it ends */
}

.loglines-modal.fade-in {
    opacity: 1;
    animation: fadeIn 200ms ease-in;
}

.loglines-modal.fade-out {
    animation: fadeOut 200ms ease-out;
}

.loglines-modal .up {
    animation: up 200ms ease-in;
}

.loglines-modal .down {
    animation: down 200ms ease-out;
}

.loglines-modal .left {
    animation: left 300ms ease-in;
}

.loglines-modal .right {
    animation: right 300ms ease-out;
}
