@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.modal-container {
  position: fixed;
  inset: 0;
  z-index: 999;
}

.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s ease;
  z-index: 40;
}
.modal-overlay.show {
  opacity: 1;
  pointer-events: auto;
  animation: show 0.2s ease-in;
}

.slide-modal {
  position: fixed;
  background: white;
  transform: translateX(100%); /* Hiding modal */
  transition: transform 0.3s ease;
  z-index: 50;
  padding: 20px;
}
.slide-modal.show {
  transform: translateX(0); /* Showing modal from right */
  animation: fadeIn 0.3s ease-in;
}