@tailwind base;
@tailwind components;
@tailwind utilities;

@import './generated-imports.style.css';

@font-face {
    font-family: 'Roboto';
    src: url('assets/fonts/roboto/Roboto-Regular.ttf');
}

@layer base {
    body {
        color: var(--loglines-gray-v6);
    }
}

@keyframes slide-in {
    0% {
        margin-top: -50px;
    }

    100% {
        margin-top: 0px;
    }
}

@keyframes slide-out {
    0% {
        margin-top: 0px;
    }

    100% {
        margin-top: -100px;
    }
}

/* Logistical Container */

.box-shadow {
    box-shadow: 0px 10px 10px #cbcaca;
}

.required:before {
    content: ' *';
    color: var(--loglines-red-v3);
    padding-right: 1px;
}

.logo-brand-svg path {
    fill: var(--loglines-brand-v5);
}

/* SideMenu */
.themes-sidebar {
    font-size: 16px;
}

/* Tab Master */
div.tab-master {
    font-size: 14px;
}

/* Table Master - Table Auto*/
table.table-master.table-auto {
    font-size: 14px;
}
