.loglines-checkbox {
    background-color: var(--loglines-white);
    color: var(--loglines-brand-v5) !important;
    border-color: var(--loglines-gray-v3);
    @apply !ring-transparent;
}
.loglines-checkbox:focus {
    @apply !ring-transparent;
}

/* -------- Variant Checkbox - Default ------------ */
.checkbox-default {
    background-color: var(--loglines-white);
    border-color: var(--loglines-gray-v3);
}
.checkbox-default:checked {
    background-color: var(--loglines-brand-v6);
    color: var(--loglines-brand-v6) !important;
}

/* -------- Variant Checkbox - Brand Inverse ------------ */
.checkbox-brand-inverse {
    background-color: var(--loglines-white);
    border-color: var(--loglines-brand-v5);
}
.checkbox-brand-inverse:checked {
    background-color: var(--loglines-brand-v4);
    color: var(--loglines-brand-v4) !important;
}
