.jamInputContainer {
    border-color: var(--loglines-gray-v3);
    @apply relative border rounded-md;
}

.jamInput {
    @apply px-10 py-2 text-center focus:outline-none rounded-md;
}

.jamIcon {
    @apply absolute inset-y-0 right-0 flex items-center pr-3 text-gray-500;
}

.jamDropdown {
    background-color: var(--loglines-white);
    @apply absolute z-50 mt-[1px] flex shadow-lg;
    left: 100%;
}

.jamSelect {
    @apply appearance-none w-full py-3 text-center cursor-pointer;
}

.jamSelect:first-child {
    border-color: var(--loglines-gray-v3);
    @apply rounded-l-md border border-r-0;
}

.jamSelect:last-child {
    border-color: var(--loglines-gray-v3);
    @apply rounded-r-md border border-l-0;
}

.jamSelect:focus {
    border-color: var(--loglines-brand-v5);
    @apply ring-1 ring-blue-500;
}

.jamSelect option {
    background-color: var(--loglines-white);
    @apply text-gray-900;
}

.jamSelect option:checked {
    @apply bg-blue-500 text-white;
}

.jamSelect::-webkit-scrollbar {
    @apply w-0 h-0;
}

input[type='time']::-webkit-calendar-picker-indicator {
    transform: scale(1.5);
}
