/* progress bar default */
.progress-bar {
    @apply flex items-center;
}

.progress-bar .bg-bar {
    background-color: var(--loglines-yellow-v2);
}

.progress-bar .bg-progress {
    background-color: var(--loglines-brand-v5);
}

.progress-bar i {
    color: var(--loglines-white);
}

/* .progress-bar-outline-green */
.progress-bar-outline-green .bg-bar {
    @apply flex flex-col;
    background-color: var(--loglines-white);
}

.progress-bar-outline-green .border-bar {
    border-color: var(--loglines-gray-v3);
    background-color: var(--loglines-white);
    @apply border-2;
}

.progress-bar-outline-green .bg-progress {
    background-color: var(--loglines-green-v2);
}

.progress-bar-outline-green i {
    color: var(--loglines-green-v4);
}

/* progress-bar-outline-blue */
.progress-bar-outline-blue .border-bar {
    background-color: var(--loglines-white);
    @apply border-2 border-sky-400;
}
