.measurement-input-container {
    @apply relative flex items-center w-full h-10;
    background-color: var(--loglines-white);
}
.measurement-input-container label {
    @apply w-fit absolute;
    @apply translate-x-2;
    padding-right: 2px;
    background-color: var(--loglines-white);
}
.measurement-input-container p {
    @apply w-fit;
    @apply text-size-XS;
    @apply -translate-y-2;
    padding-left: 3px;
    color: var(--loglines-gray-v5);
}

.measurement-input-container .unit {
    @apply relative border-l-0 rounded-r p-2 flex items-center justify-between text-size-S cursor-pointer;
    height: inherit;
}

.measurement-input-container .dropdown-list {
    @apply p-0 m-0 border absolute w-full z-[11] rounded top-full left-0 max-h-[123px] overflow-x-auto mt-3 text-size-S;
    border-color: var(--loglines-brand-v5);
    background-color: var(--loglines-white);
}

.measurement-input-container input {
    @apply p-2 flex-grow border-none rounded-l w-full h-inherit text-size-S;
    border-color: var(--loglines-gray-v3);
}

.measurement-input-container input::placeholder {
    color: var(--loglines-gray-v4);
}

/* Chrome, Safari, Edge, Opera */
.measurement-input-container input[type='number']::-webkit-outer-spin-button,
.measurement-input-container input[type='number']::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
.measurement-input-container input[type='number'] {
    -moz-appearance: textfield;
}

.measurement-input-container .disabled-gray {
    color: var(--loglines-gray-v6);
    background-color: var(--loglines-gray-v1);
}

.measurement-input-container .disabled-white {
    color: var(--loglines-gray-v5);
    background-color: var(--loglines-white);
}
