/* tracker bg blue */
.tracker-bg-blue {
    background-color: var(--loglines-brand-v2);
}
.tracker-bg-blue .active .circle {
    background-color: var(--loglines-brand-v6);
}

.tracker-bg-blue .active .line {
    background-color: var(--loglines-brand-v6);
}

.tracker-bg-blue .active .item-status {
    color: var(--loglines-brand-v6);
}

.tracker-bg-blue .active .item-description {
    font-weight: bold;
    color: var(--loglines-gray-v6);
}

.tracker-bg-blue .inactive .circle {
    background-color: var(--loglines-gray-v5);
}

.tracker-bg-blue .inactive .line {
    background-color: var(--loglines-gray-v5);
}

.tracker-bg-blue .inactive .item-status {
    color: var(--loglines-gray-v5);
}

.tracker-bg-blue .inactive .item-description {
    @apply font-bold;
    color: var(--loglines-gray-v5);
}
.tracker-bg-blue .inactive .item-details {
    color: var(--loglines-gray-v5);
}

/* tracker bg white */
.tracker-bg-white {
    background-color: var(--loglines-white);
}
.tracker-bg-white .active .circle {
    background-color: var(--loglines-brand-v7);
}

.tracker-bg-white .active .line {
    background-color: var(--loglines-brand-v7);
}

.tracker-bg-white .active .item-status {
    color: var(--loglines-brand-v6);
}

.tracker-bg-white .active .item-description {
    color: var(--loglines-gray-v6);
}

.tracker-bg-white .inactive .circle {
    background-color: var(--loglines-gray-v5);
}

.tracker-bg-white .inactive .line {
    background-color: var(--loglines-gray-v5);
}

.tracker-bg-white .inactive .item-status {
    color: var(--loglines-gray-v5);
}

.tracker-bg-white .inactive .item-description {
    color: var(--loglines-gray-v5);
}
