    background-color: var(--loglines-green-v1);
    color: var(--loglines-green-v4);
}

.themes-status-green:hover {
    color: var(--loglines-green-v4);

    span {
        background-color: var(--loglines-green-v1);
        color: var(--loglines-green-v4);
    }
}

.themes-status-green.active,
.themes-status-green.active:hover {
    color: var(--loglines-green-v4);
}

